.header {
  &__image-container {
    overflow: hidden;
  }
  
  &__image {
    
    object-fit: contain;
    object-position: center;
    width: 100%;
    margin-bottom: 20px;

    @include onTablet {
      position: absolute;
      width: calc((100% - 210px) * 0.5 + 90px);
      max-width: 444px;
    }
    @include onDesktop {
      position: absolute;
      width: calc((100% - 410px) * 7 / 12 + 220px);
      max-width: 665px;
    }
  }

  &__burger {
    position: absolute;
    background-image: url(/images/logos/Burger-logo.png);
    width: 18px;
    height: 12px;
    top: 33px;
    left: 23px;
    transition: transform $animationSpeed;
  
    &:hover {
      transform: scale(1.2);
    }

    @include onTablet {
      top: 45px;
      left: auto;
    }

    @include onDesktop {
      top: 45px;
      left: auto;
    }
  }

  &__logo {
    position: absolute;
    text-align: center;
    top: 8px;
    left: 50%;
    transform: translateX(-50%);
    
    @include onTablet {
      font-size: 36px;
      line-height: 36px;
      position: relative;
      top: 16px;
    }

    @include onDesktop {
      position: relative;
      font-size: 36px;
      line-height: 36px;
      top: 16px;
    }
  }
}
